import axios from 'axios'

const _defaultLanguange = "ru-RU"
const API_KEY = '61535d35d8f1f0f5cd6bcb40fd8046f6'

export default class MediaService {
   
    static async getPopularMedia(_page = 1, type='movie') {
        const response = await axios.get(`https://api.themoviedb.org/3/${type}/popular?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getTopRatedMedia(_page = 1, type='movie') {
        const response = await axios.get(`https://api.themoviedb.org/3/${type}/top_rated?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getNowPlayingMovie(_page = 1) {
        const response = await axios.get(`https://api.themoviedb.org/3/movie/now_playing?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getUpcomingMovie(_page = 1) {
        const response = await axios.get(`https://api.themoviedb.org/3/movie/upcoming?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getAiringTodayTV(_page = 1) {
        const response = await axios.get(`https://api.themoviedb.org/3/tv/airing_today?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getOnTheAirTV(_page = 1) {
        const response = await axios.get(`https://api.themoviedb.org/3/tv/on_the_air?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getTrending(_page = 1, time_window='day', media_type='movie') {
        const response = await axios.get(`https://api.themoviedb.org/3/trending/${media_type}/${time_window}?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
            }
        })
        return response
    }


    //======================================================================================================================

    static async getFilmsGenres() {
        const response = await axios.get(`https://api.themoviedb.org/3/genre/movie/list?api_key=${API_KEY}`, {
            params: {
                language: _defaultLanguange,
            }
        })
        return response
    }

    static async getDiscoverMovies(_page, _genres, _voteFilter, _raitingFilter, _yearFilter) {
        const response = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=${API_KEY}`, {
            params: {
                page: _page,
                language: _defaultLanguange,
                with_genres: _genres,
                'vote_count.gte': _voteFilter,
                'vote_average.gte': _raitingFilter[0,0],
                'vote_average.lte': _raitingFilter[0,1],
                'primary_release_date.gte': new Date(_yearFilter[0,0],0,1),
                'primary_release_date.lte': new Date(_yearFilter[0,1],0,1),
            }
        })
        return response
    }

    //======================================================================================================================



    static async getMediaTrailer(id, type='movie') {
        const response = await axios.get(`https://api.themoviedb.org/3/${type}/${id}/videos?api_key=${API_KEY}`, {
            params: {
                language: _defaultLanguange
            }
        })
        return response
    }

    static async getMediaByTitle(title, type='movie') {
        const response = await axios.get(`https://api.themoviedb.org/3/search/${type}?api_key=${API_KEY}`, {
            params: {
                language: _defaultLanguange,
                query: title
            }
        })
        return response
    }

    static async getMediaById(id, type='movie') {
        const response = axios.get(`https://api.themoviedb.org/3/${type}/${id}?api_key=${API_KEY}`, {
            params: {
                language: _defaultLanguange
            }
        })
        return response
    }
}