import React from "react";
import MediaService from "./API/MediaService";
import useFetching from "./hooks/useFetching";
import Footer from './pages/footer/Footer';
import Header from './pages/header/Header';
import MainPage from "./pages/main/MainPage";
import MovieContext from './context/MovieContext'
import Dalay from './utils/Dalay'
import SearchPage from "./pages/search/SearchPage";
import {Routes,Route, useNavigate} from 'react-router-dom'
import Seriales from "./pages/seriales/Seriales";
import MediaPage from "./pages/media/MediaPage";
import MoviesPage from './pages/movies/MoviesPage'

import './styles/sass/style.scss'
function App() {

  //#region  serch movies
  const [searchQuery, setSearchQuery] = React.useState('')
  const [searchMovies, setSearchMovies] = React.useState([])
  const [searchQueryForTitle, setSearchQueryForTitle] = React.useState('')
  const [fetchSearchQuery, isSearchQueryLoading, errorSearchQueryValue] = useFetching( async () => {
     const response = await MediaService.getMediaByTitle(searchQuery)
     await Dalay.wait(1)
     setSearchMovies(response.data.results)
  })

  let navigate = useNavigate()
  function getFilmsByQuery(e){
    e.preventDefault()

      if(searchQuery){
        navigate('/search')
          setSearchQueryForTitle(searchQuery)
          setSearchQuery('')

        fetchSearchQuery()
      }
  }
  //#endregion

  //#region latest movies
  const [sliderMovies, setSliderMovies] = React.useState([])
  const [fetchSliderMovies, isSliderLoading, errorSliderMovies] = useFetching(async () => {
    const response = await MediaService.getTrending()
    await Dalay.wait(1)
    setSliderMovies(response.data.results)
  })

  React.useState(() => {
    fetchSliderMovies()
  },[])
  //#endregion

  return (
    <div className="wrapper _container">
      <MovieContext.Provider value={{searchQuery, setSearchQuery, getFilmsByQuery, searchMovies, isSearchQueryLoading, searchQueryForTitle,
                                     sliderMovies, isSliderLoading}}>

          <Header/> 
           <div className="page">
              <Routes>
                  <Route path="/search" element={<SearchPage/>}/>
                  <Route path="/media/:id" element={<MediaPage/>}/>
                  <Route path="/seriales" element={<Seriales/>}/>
                  <Route path="/" element={<MainPage/>}/>
                  <Route path="/movies" element={<MoviesPage/>}/>
              </Routes>
          </div>
          <Footer/>

       </MovieContext.Provider>
    </div>
  );
}

export default App;
